// @flow
import React from 'react';
import {
  Link,
  withRouter,
} from 'react-router-dom';
import cx from 'classnames';
import s from './ArchiveCover.scss';
import ArchiveImage from '../ArchiveImage';
import noCover from '../../assets/no_cover.png';

type P = {
  archive: ArchiveHomeInfo,
  location: Location,
}

const ArchiveCover = ({ archive, location } : P) => {
  const image = archive.image ? archive.image : noCover;
  const tags = archive.tags.map(tag => (
    <div className={s.tag} key={tag}>{`${tag}`}</div>
  ));
  return (
    <div className={cx(s.gallery, archive.extracted ? s.extracted : null)}>
      <Link to={`/${archive.id}/img/1${location.search}`}>
        <div className={s.coverTitle} title={archive.title}>
          {archive.title}
        </div>
        <ArchiveImage className={s.imageContainer} src={image} title={archive.title} width={archive.image_width} height={archive.image_height} />
        <div className={s.overlay}>
          <div className={s.tags}>
            {tags}
          </div>
        </div>
      </Link>
      <div>
        <div className={s.caption}>
          {`${archive.filecount} | `}
          <a className={cx(s.link, s.highlight)} href={`${archive.url}`}>SRC</a>
        </div>
      </div>
    </div>
  );
};

export default withRouter(ArchiveCover);
