// @flow
import qs from 'qs';
import config from '../config';

export async function getArchiveImages(archiveId: string, positions: Array<number>): Promise<Array<ImgCacheElement>> {
  const queryPositions = qs.stringify({ i: positions }, { indices: false });
  let apiText;
  if (config.PandaBackupKey === '') {
    apiText = ''
  } else {
    apiText = `&api_key=${config.PandaBackupKey}`;
  }
  const query = `${config.PandaBackupUrl}/archive/${archiveId}/image_list/?${queryPositions}${apiText}`;
  const response = await fetch(query,
    {
      credentials:   'include',
    });
  return await response.json();
}

export async function getArchiveExtendedComponent(archiveId: string, position: number): Promise<ArchiveExtendedInfo> {
  const correctedPosition = position || 1;
  let apiText;
  if (config.PandaBackupKey === '') {
    apiText = ''
  } else {
    apiText = `&api_key=${config.PandaBackupKey}`;
  }
  const query = `${config.PandaBackupUrl}/archive/${archiveId}/?position=${correctedPosition}${apiText}`;
  const response = await fetch(query,
    {
      credentials:   'include',
    });
  return await response.json();
}

export async function getArchivesComponent(queryString: string): Promise<ArchiveObjectContainer> {
  let apiText;
  if (queryString === '') {
    apiText = `?api_key=${config.PandaBackupKey}`;
  } else {
    apiText = `&api_key=${config.PandaBackupKey}`;
  }
  const query = `${config.PandaBackupUrl}/archives/${queryString}${apiText}`;
  const response = await fetch(query,
    {
      credentials:   'include',
    });
  return await response.json();
}
