// @flow
import React from 'react';
import { Button } from 'reactstrap';
import ToggleButton from '../ToggleButton';

type Props = {
  displayControls: boolean,
  toggleControls: () => void,
  className?: string,
  controls: Array<{key: string, name: string, description: string, action?: Function}>
}

const SidebarControls = ({displayControls, toggleControls, className, controls}: Props) => (
  <div className={className}>
    {
      <div>
        {
          displayControls ? controls.map(
            (control) => (
              <div key={control.key}>
                <Button onClick={control.action} color="primary" size="sm" block>{control.description}</Button>
              </div>
            ),
          ) : null
        }
        <ToggleButton onClick={toggleControls} showContent={displayControls} alignRight={true}/>
      </div>
    }
  </div>
);

export default SidebarControls;