// @flow
import React from 'react';

type AImage = {src: string, title: string, height: number, width: number, className: string };

const ArchiveImage = ({src, title, height, width, className }: AImage) => {
  const defaults = {
    maxHeight: 290,
    width,
    // backgroundColor: 'gray',
  };

  const imgStyle = {
    height: 'auto',
    maxWidth: '100%',
    maxHeight: height,
    border: '1px solid #ccc',
  };

  return (<div className={className} style={{ ...defaults }}>
    <img style={imgStyle} src={src} alt={title} height={height} width={width} />
  </div>);
};

ArchiveImage.defaultProps = {
  width: 200,
  height: 290,
};

export default (ArchiveImage);
